* {
  margin: 0;
  padding: 0;
}
.currentWheather {
  display: flex;
  margin: auto;
  border: 2px outset blue;
  border-radius: 8px;
  width: 800px;
  margin-bottom: 10px;
}
.imgCurrent {
  width: 300px;
}

.infoCurrent {
  padding-top: 20px;
  line-height: 30px;
}

.containerItem {
  width: 600px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  text-align: center;
}
.navBar {
  display: flex;
  justify-content: space-between;
}
.buttonSearch {
  margin-right: 10px;
  display: flex;
  position: "relative";
  background-color: rgb(245, 241, 9);
  border-radius: 8px;
  width: "100%";
}
.titelName {
  margin-left: 10px;
}

.buttonSearch:hover {
  background-color: burlywood;
  color: white;
}
